import styled from "styled-components";
import { useState } from "react";

import { A } from "../../assets/styles/component";

import Select from "../../assets/UI/Select";
import { Burger } from "./burger";

import logo_desk from "../../assets/images/logo_desktop.svg";

import { EngLoc } from "../../assets/constants/localizations";

const StyledHeader = styled.header`
  display: flex;
  justify-content: space-between;
  flex-direction: column;

  width: 100vw;
  height: 3.938rem;
  padding: 1.625rem 1rem;
  box-sizing: border-box;

  font-weight: 400;
  font-size: 1.125rem;

  transition: 0.25s;

  &,
  & *:not(div) {
    z-index: 12;
  }
`;

const Nav = styled.nav`
  display: flex;
  flex-direction: column;

  justify-content: space-between;
  gap: 1.25rem;

  max-width: 100vw;
  padding: 0.938rem 0.938rem;
  margin: 0.938rem 0;

  background: rgba(71, 8, 204, 0.85);

  ${({ open = false }) =>
    open
      ? ` opacity: 1;`
      : `opacity: 0;
      user-select: none;
      pointer-events: none;
      height: 0;
      overflow:hidden;
  `}

  text-align: center;
  transition: 0.5s;
`;

const MobileNav = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;

  & > div {
    display: flex;
    align-items: center;
    gap: 1.875rem;
  }

  & > div:not(:first-child) > div {
    height: 1rem;
  }

  & svg .rect {
    fill-opacity: 0.08;
    transition: 0.5s;
  }

  & svg:active .rect {
    fill: #ffffff;
    fill-opacity: 0.2;
  }

  & svg .row1 {
    transition: 0.4s;
    ${({ open = false }) =>
      open && `transform: translateX(15px) translatey(-6px) rotate(45deg);`}
  }

  & svg .row3 {
    transition: 0.4s;
    ${({ open = false }) =>
      open &&
      `transform: translateX(-0.625rem) translatey(1rem) rotate(-45deg);`}
  }
  & svg .row2 {
    transition: 0.25s;
    ${({ open = false }) => open && `opacity: 0;`}
  }
`;

const NavTabs = styled.div`
  display: flex;
  flex-direction: column;
  gap: 0.938rem;

  & > a {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 3.125rem;
  }
`;

const Logo = styled.img`
  aspect-ratio: 81 / 37;
  margin-top: -0.625rem;
`;

const Header = ({ navItem, language, scrollY, changeLng }) => {
  const [openNav, setOpenNav] = useState(false);

  const changeLocalization = (lang = "Ru") => {
    localStorage.setItem("Language", lang);
    changeLng(lang);
  };

  return (
    <StyledHeader scrollY={scrollY}>
      <MobileNav open={openNav}>
        <div>
          <div onClick={() => setOpenNav(!openNav)}>{Burger}</div>
        </div>
        <Logo
          width="81"
          height="37"
          loading="lazy"
          src={logo_desk}
          alt="За стеклом"
        />
        <Select
          type="languages"
          name={language === "Ru" ? "Ru" : "Eng"}
          paddingX="17"
          color="#FFFFFF"
          bg_color="rgba(255, 255, 255, 0.03)"
          border={true}
          onChange={changeLocalization}
        ></Select>
      </MobileNav>
      <Nav open={openNav}>
        <NavTabs>
          {navItem.map((item, index) => (
            <A
              href={item.href}
              size="18"
              key={index}
              target={item.blank && "_blank"}
              aria-label={"Перейти на раздел " + item.name}
            >
              {item.name}
            </A>
          ))}
        </NavTabs>
      </Nav>
    </StyledHeader>
  );
};

export default Header;
